import { Component, Input } from '@angular/core';

@Component({
  selector: 'aui-logo',
  template: `<a class="o-header__logo" href="{{link || '#'}}" title="{{title}}" (click)="onClick($event)">
    <img [src]="src"[alt]="title" />
</a>

`,
})
export class LogoComponent {
  @Input()
  title = 'Default';

  @Input()
  src = 'https://place-hold.it/170x170';

  @Input()
  link ? = '/';

  @Input()
  onClick?: Function;
}
